<template>

  <div>
    <el-row>
      <el-col :span="4">
        <el-button>当日请求:</el-button>
      </el-col>
      <el-col :span="4">
        <el-button type="primary">{{request_url_day_count}}</el-button>
      </el-col>
    </el-row>
    <el-row style="margin-top: 10px">
      <el-col :span="4">
        <el-button>总请求:</el-button>
      </el-col>
      <el-col :span="4">
        <el-button type="primary">{{request_url_all_count}}</el-button>
      </el-col>
    </el-row>
    <el-row style="margin-top: 10px">

      <el-carousel :interval="4000" type="card" height="200px">
        <el-carousel-item v-for="item in 6" :key="item">
          <div class="banner_p">
            <img src="http://po8c2o94f.bkt.clouddn.com/%E5%BE%AE%E4%BF%A1%E5%9B%BE%E7%89%87_20190304163943.png"
                 width="100" height="100"/>
          </div>
        </el-carousel-item>
      </el-carousel>

      <span>{{ info }}</span>


    </el-row>
  </div>
</template>

<script>

  import Top_Type from "./Top_Type";

  const axios = require('axios');


  export default {
    name: "Tab1",
    components: {Top_Type},
    data() {
      return {
        request_url_day_count: 100,
        request_url_all_count: 1000,
        info: ''
      }
    },
    mounted() {
      console.log('mounted ok begin http request')

      this.$jsonp('https://api.shenjian.io', {
        appid: '560d2f61da546eee0ea2b506ba572508',
        codes: '002421'
      }).then(response => {
        console.log(response)
        this.info = response.data;
      }).catch(e => {

      })

    }
  }
</script>

<style scoped>

  .el-button {
    width: 100px;
  }

  .el-col {
    display: flex;
    margin-left: 10px;
  }

  .el-carousel__item h3 {
    color: #475669;
    font-size: 14px;
    opacity: 0.75;
    line-height: 200px;
    margin: 0;
  }

  .el-carousel__item:nth-child(2n) {
    background-color: #99a9bf;
  }

  .el-carousel__item:nth-child(2n+1) {
    background-color: #d3dce6;
  }

  .banner_p {
    display: flex;
    justify-content: center;
    align-items: center;

    height: 100%;
  }
</style>



